.main_wrapper {
    position: relative;
    user-select: none;
    width: 40px;
    height: 40px;

    .dots_wrapper {
        background: rgba(148, 148, 148, 0.638);
        width: 100%;
        height: 100%;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;

        position: absolute;
        cursor: pointer;

        &:hover {
            background: rgba(148, 148, 148, 0.823);
        }
        &:active {
            background: rgba(148, 148, 148, 0.96);
        }

        .dots {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .dot {
                height: 3.5px;
                width: 3.5px;
                border-radius: 50%;
                background: rgb(0, 0, 0);
                margin: 2px;
            }
        }
    }
    .content_wrapper {
        background: green;
        z-index: 20;
        position: absolute;
        right: 0;
        top: 0;
        box-shadow: -3px 5px 10px 5px rgba(0, 0, 0, 0.768);

        .content {
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
        }
    }
    .black_space {
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background: rgba(0, 0, 0, 0.357);
        position: fixed;
        z-index: 10;
    }
}
