.top_wrapper {
    background: rgba(0, 23, 151, 0.891);

    width: 100%;
    height: 60px;
    box-sizing: border-box;
    padding: 0 5px;

    .top_content {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
.main {
    box-sizing: border-box;
    padding: 0 10px;

    display: flex;
    flex-direction: column;
    align-items: center;

    .content {
        width: 100%;
        display: flex;
        flex-direction: column;

        background: rgba(53, 53, 53, 0.35);

        box-sizing: border-box;
        padding: 0 10px;

        @media screen and (min-width: 600px) {
            width: 500px;
        }

        .title {
            text-transform: capitalize;
            color: blue;
        }
        .username {
            margin-bottom: 5px;
        }
        .username,
        .bio {
            display: flex;
            flex-direction: column;

            padding: 5px 0;
        }
        .username .dlpfpfwk,
        .bio .dlpfpfwk {
            font-size: 10px;
            display: inline-flex;

            text-transform: capitalize;
        }
        .bio {
            width: 100%;
            position: relative;

            .bio_text {
                white-space: nowrap;
                overflow: hidden;
            }
        }
        .item {
            text-transform: uppercase;
            box-sizing: border-box;
            padding: 5px 0;
            border-top: 1px solid;
            border-bottom: 1px solid;
            border-color: rgba(255, 255, 255, 0.289);
            // background: rgba(7, 0, 145, 0.775);
        }
    }
}
